header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 2;
    background-color: #fff;
    font-family: Arial, Helvetica, sans-serif;
    transition: .3s;
}

.header-body {
    display: flex;
    justify-content: space-between;
    height: 60px;
    align-items: center;

    &.computer {
        nav {
            display: flex;
            align-items: center;
    
            .logo {
                a {
                    display: flex;
                    align-items: center;
                    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    
                    img {
                        width: 40px;
                        margin-right: 15px;
                        border-radius: 7px;
                    }
                }
            }
    
            .header-list {
                display: flex;
    
                li {
                    margin-left: 20px;
                    position: relative;
    
                    &.active::before {
                        content: "";
                        position: absolute;
                        bottom: -2px;
                        right: 0;
                        left: 0;
                        height: 2px;
                        border-radius: 2px;
                        background-color: #3e6b48;
                    }
    
                    a {
                        color: #444444;
                        transition: .3s;
    
                        &:hover {
                            color: #000;
                        }
                    }
                }
            }
        }
    }

    &.mobile {
        .logo {
            a {
                display: flex;
                align-items: center;
                font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;

                img {
                    width: 40px;
                    margin-right: 15px;
                    border-radius: 7px;
                }
            }
        }

        nav {
            display: flex;
            align-items: center;
    
            .header-list {
                li {
                    position: relative;
    
                    &.active::before {
                        display: none;
                    }
    
                    a {
                        color: #444444;
                        transition: .3s;
    
                        &:hover {
                            color: #000;
                        }
                    }
                }
            }
        }
    }
}

.header-btns {
    display: flex;
    align-items: center;

    .header-dwnl-btn, .header-ds-btn {
        padding: 10px;
        border-radius: 7px;
        cursor: pointer;
        margin-left: 10px;
    }

    .header-dwnl-btn {
        color: #fff;
        background-color: #3e6b48;
        transition: .3s background-color;

        &:hover {
            background-color: #2a4c31;
        }
    }

    .header-ds-btn {
        border: 1px solid #3e6b48;
        color: #2a4c31;
        transition: .3s;

        &:hover {
            background-color: #3e6b48;
            color: #fff;
            transition: .3s;
        }
    }

    .theme-btn {
        padding: 10px;
        border-radius: 7px;
        cursor: pointer;
        transition: .3s;
        
        &:hover {
            background-color: #3e6b48;
            transition: .3s;

            svg path {
                fill: #fff;
            }
        }
    }
}

.mobile {
    display: none;
}

@media(max-width:768px) {
    .computer {
        display: none;
    }

    .mobile {
        display: flex;
    }

    .hamburger {
        display: block;
        position: relative;
        width: 30px;
        height: 20px;
        cursor: pointer;
        z-index: 4;
        margin-left: 20px;

        &::before, &::after {
            content: "";
            background-color: #000;
            position: absolute;
            width: 100%;
            height: 2px;
            left: 0;
            transition: .5s;
        }

        &::before {
            top: 0;
        }

        &::after {
            bottom: 0;
        }

        span {
            position: absolute;
            left: 0;
            width: 100%;
            height: 2px;
            background-color: #000;
            top: 9px;
            transition: .5s;
        }

        &.active::before {
            transform: rotate(45deg);
            top: 9px;
        }

        &.active::after {
            transform: rotate(-45deg);
            top: 9px;
        }

        &.active span {
            transform: scale(0);
        }
    }

    .header-body nav {
        position: fixed;
        right: -100vw;
        top: 0;
        background-color: #fff;
        height: 100vh;
        width: 250px;
        display: block;
        border-left: #b7b7b7 solid 3px;
        z-index: 3;
        transition: .3s right;

        &.active {
            right: 0;
        }

        .header-list {
            position: absolute;
            top: 80px;
            left: 20px;
            right: 20px;
            margin-left: 0;
        }

        .header-list li {
            margin-left: 0;
            margin-right: 0;
            margin-bottom: 10px;

            a {
                border-radius: 7px;
                padding: 10px;
                display: block;
                width: 100%;
                color: #000;
                transition: .3s background-color;

                &:hover {
                    background-color: #ddd;
                }
            }
        }

        .header-btns {
            position: absolute;
            bottom: 20px;
            right: 20px;
            display: flex;
            justify-content: right;

            .auth-btn {
                margin-right: 0;
            }
        }
    }
}