.adven-item {
    text-align: center;
    border-radius: 15px;
    background-color: #3e6b48;
    background: linear-gradient(to right bottom, #3e6b48, #2a4c31);
    padding: 20px;
    width: 300px;
    margin-left: 30px;
    margin-right: 30px;
    margin-top: 60px;
    cursor: pointer;
    transition: .3s transform;

    &:hover {
        transform: scale(102%);
    }

    svg {
        margin-bottom: 20px;
        height: 200px;
    }

    h3 {
        color: #fff;
    }
}