.dl {
    padding-top: 200px;

    .demon-item {
        display: flex;
        align-items: center;
        padding: 20px;
        margin-bottom: 40px;
        border: 1px solid #ddd;
        border-radius: 15px;
        cursor: pointer;
        position: relative;
        transition: .3s transform;

        &:hover {
            transform: scale(102%);
        }

        &::before {
            content: "";
            position: absolute;
            top: 0;
            right: 0;
            background-color: #3e6b48;
            background: linear-gradient(to right, #3e6b48, #2a4c31);
            width: var(--random-width);
            height: 50px;
            border-radius: 2px;
            border-top-right-radius: 15px;
            border-bottom-left-radius: 70px;
            z-index: 1;
        }

        img {
            width: 200px;
            height: 120px;
            border-radius: 15px;
            margin-right: 30px;
        }

        .points {
            position: absolute;
            bottom: 20px;
            right: 20px;
        }
    }
}

@media(max-width:650px) {
    .dl {
        padding-top: 200px;
        font-size: 14px;
    
        .demon-item {
            display: flex;
            align-items: center;
            padding: 20px;
            margin-bottom: 40px;
            border: 1px solid #ddd;
            border-radius: 15px;
            cursor: pointer;
            position: relative;
            transition: .3s transform;
    
            &:hover {
                transform: scale(102%);
            }
    
            &::before {
                height: 25px;
                z-index: -1;
            }
    
            img {
                width: 150px;
                height: 90px;
                border-radius: 15px;
                margin-right: 20px;
            }
    
            .points {
                position: absolute;
                bottom: 20px;
                right: 20px;
            }
        }
    }
}