.f-screen {
    padding-top: 200px;
    padding-bottom: 200px;
    font-size: 16px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .offer {
        max-width: 600px;

        h1, p {
            margin-bottom: 20px;
        }

        button {
            padding: 10px;
            border-radius: 7px;
            cursor: pointer;
            width: 150px;
            background-color: #3e6b48;
            color: #fff;
            font-size: 16px;
            font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
            transition: .3s background-color;
            
            &:hover {
                background-color: #2a4c31;
            }
        }
    }

    .f-screen-img {
        max-width: 700px;
        min-width: 700px;
        margin-left: 60px;
        position: relative;

        &::before {
            content: '';
            position: absolute;
            top: 60%;
            left: 65%;
            width: 450px;
            height: 450px;
            background-color: #3e6b48;
            border-radius: 20%;
            transform: translate(-50%, -50%) rotate(45deg);
            z-index: 0;
        }

        img {
            max-width: 100%;
            border-radius: 15px;
            box-shadow: 0 0 1rem #0000007d;
            position: relative;
            z-index: 1;
        }
    }
}

.home-dl-bg {
    padding-top: 100px;
    padding-bottom: 100px;
    background-color: #ebebeb;
    margin-bottom: 300px;
}

.home-dl {
    font-size: 16px;
    display: flex;
    align-items: center;

    .home-dl-img {
        max-width: 600px;
        min-width: 600px;
        margin-right: 60px;
        position: relative;

        img {
            max-width: 100%;
            border-radius: 15px;
            border: 1px solid #d5d5d5;
        }
    }

    .home-dl-text {
        h1, p {
            margin-bottom: 20px;
        }
    }
}

.home-icons-list-bg {
    margin-top: 100px;
    margin-bottom: 300px;

    .home-icons-text {
        display: flex;
        justify-content: space-between;
    }

    .home-icons-text-dopolnitelno {
        display: flex;
        align-items: flex-end;
        justify-content: flex-end;
    }

    h2 {
        color: #767676;
    }
}

.home-icons-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    padding-top: 80px;
}

.discord {
    margin-top: 200px;
    display: flex;
    align-items: center;

    .discord-img {
        max-width: 600px;
        margin-left: 60px;

        img {
            max-width: 100%;
            border-radius: 15px;
            border: 1px solid #d5d5d5;
        }
    }

    .discord-offer {
        h1, p {
            margin-bottom: 20px;
        }

        button {
            padding: 10px 30px;
            border-radius: 7px;
            cursor: pointer;
            background-color: #3e6b48;
            color: #fff;
            font-size: 16px;
            font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
            transition: .3s background-color;
            
            &:hover {
                background-color: #2a4c31;
            }
        }
    }
}

.fruitspace {
    margin-top: 200px;
    display: flex;
    align-items: center;

    .fruitspace-img {
        max-width: 600px;
        min-width: 600px;
        margin-right: 60px;

        img {
            max-width: 100%;
            border-radius: 15px;
            border: 1px solid #d5d5d5;
        }
    }

    .fruitspace-text {
        h1, p {
            margin-bottom: 20px;
        }
    }
}

.admins-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    margin-top: 80px;
    padding-bottom: 200px;
}

@media(max-width:1300px) {
    .f-screen {
        flex-direction: column;

        .offer {
            max-width: 100%;
        }

        .f-screen-img {
            margin-top: 100px;
            margin-left: 0;
            max-width: 90%;
            min-width: 0%;

            &::before {
                top: 60%;
                left: 65%;
                width: 80%;
                height: 80%;
            }
        }
    }
}

@media(max-width:1100px) {
    .home-dl {
        flex-direction: column;
    
        .home-dl-img {
            max-width: 90%;
            min-width: 90%;
            margin-right: 0;
            position: relative;
            margin-bottom: 60px;
            padding-left: 20px;
            padding-right: 20px;
    
            img {
                max-width: 100%;
                border-radius: 15px;
                border: 1px solid #d5d5d5;
            }
        }
    
        .home-dl-text {
            text-align: center;
        }
    }

    .discord {
        flex-direction: column;
    
        .discord-img {
            max-width: 90%;
            min-width: 90%;
            margin-left: 0;
            margin-top: 100px;
        }
    
        .discord-offer {
            text-align: center;
        }
    }

    .fruitspace {
        flex-direction: column;
    
        .fruitspace-img {
            max-width: 90%;
            min-width: 90%;
            margin-right: 0;
            margin-bottom: 60px;
            padding-left: 20px;
            padding-right: 20px;
    
            img {
                max-width: 100%;
                border-radius: 15px;
                border: 1px solid #d5d5d5;
            }
        }
    
        .fruitspace-text {
            text-align: center;
        }
    }
}

@media(max-width:650px) {
    .home-icons-list-bg {
        margin-top: 100px;
        margin-bottom: 300px;
    
        .home-icons-text {
            display: block;
        }
    
        .home-icons-text-dopolnitelno {
            display: block;
            margin-top: 20px;
        }
    
        h2 {
            color: #767676;
        }
    }
}