.leaders-list {
    border: 1px solid #ddd;
    border-radius: 15px;

    h2 {
        text-align: center;
        background-color: #ddd;
        padding: 10px;
        border-top-left-radius: 15px;
        border-top-right-radius: 15px;
    }

    li {
        padding: 20px;
        transition: .3s background-color;

        &:hover {
            background-color: #ddd;
        }

        &:last-child {
            border-bottom-left-radius: 15px;
            border-bottom-right-radius: 15px;
        }
    }
}