.lists-list {
    padding-top: 200px;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    padding-bottom: 50px;

    li {
        text-align: center;
        border: 1px solid #ddd;
        border-radius: 15px;
        padding: 20px;
        cursor: pointer;
        margin-left: 30px;
        margin-right: 30px;
        margin-bottom: 100px;
        transition: .3s transform;
        
        &:hover {
            transform: scale(102%);
        }

        img {
            width: 300px;
            height: 180px;
            border-radius: 15px;
        }

        h3 {
            margin-top: 20px;
            color: #000;
        }
    }
}

@media(max-width:400px) {
    .lists-list li img {
        width: 160px;
        height: 90px;
    }
}