header.dark {
    background-color: #000000;
    border-bottom: 1px solid #3d3d3d;
    transition: .3s;

    .header-body {
        .logo a h3 {
            color: #fff;
        }

        .header-list li {
            &.active::before {
                background-color: #bababa;
            }

            a {
                color: #bababa;

                &:hover {
                    color: #fff;
                }
            }
        }
    }

    .header-btns {
        .header-dwnl-btn {
            color: #000;
            background-color: #fff;
            transition: .3s background-color;
    
            &:hover {
                background-color: #bababa;
            }
        }
    
        .header-ds-btn {
            border: 1px solid #bababa;
            color: #bababa;
            transition: .3s;
    
            &:hover {
                background-color: #fff;
                color: #000;
                transition: .3s;
            }
        }
    
        .theme-btn {
            padding: 10px;
            border-radius: 7px;
            cursor: pointer;
            transition: .3s;
            
            &:hover {
                background-color: #1e1e1e;
                transition: .3s;
    
                svg path {
                    fill: #fff;
                }
            }
        }
    }
}

@media(max-width:768px) {
    header.dark {
        .hamburger {
            &::before, &::after, span {
                background-color: #fff;
            }
        }
    
        .header-body nav {
            background-color: #000;
            border-left: #3d3d3d solid 1px;
    
            .header-list li {
                a:hover {
                    background-color: #141414;
                }
            }
        }
    }
}