.admin {
    padding: 20px;
    background-color: #ebebeb;
    border-radius: 15px;
    text-align: center;
    cursor: pointer;
    margin-bottom: 60px;
    margin-left: 30px;
    margin-right: 30px;
    transition: .3s;

    &:hover {
        transform: scale(102%);
        transition: .3s;

        img {
            filter: none;
        }
    }

    img {
        border-radius: 50%;
        filter: grayscale(1);
        width: 200px;
        height: 200px;
        margin-bottom: 20px;
        transition: .3s filter;
    }

    h3 {
        padding: 5px;
        background-color: #3e6b48;
        color: #fff;
        border-radius: 7px;
        margin-bottom: 5px;
    }
}